import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Link, Typography } from "@rp/react/web/mui/core"
import { ExternalFavicon } from "./ExternalFavicon"
import { removeSnippetEllipsisSpace } from "./removeSnippetEllipsisSpace"

type Props = {
  title: string
  faviconUrl: string
  name: string
  url: string
  body: string
}
const ExternalAuditCard = ({ title, faviconUrl, name, url, body }: Props) => {
  const { isMobile } = useDeviceInfo()
  return (
    <Link
      href={url}
      target="_blank"
      rel="noopener noreferrer"
      draggable="false"
      sx={{
        color: "inherit",
        p: 1.5,
        mb: 1,
        transition: "background 0.3s",
        cursor: "pointer",
        display: "block",
        borderRadius: 1,
        bgcolor: "elevation.dp01",
        overflow: "hidden",
        userSelect: "text",
        "&:hover": {
          bgcolor: "overlay.surface",
          textDecoration: "none",
        },
        "&:visited": {
          color: "inherit",
        },
      }}
    >
      <EllipsisTooltip
        typographyProps={{ sx: { fontSize: 18, color: "blue.200" } }}
        vertical
        numberOfLines={2}
        tooltipText={title}
      >
        {title}
      </EllipsisTooltip>
      <Box
        sx={{
          display: "flex",
          gap: 0.5,
          alignItems: "center",
          mb: 0.5,
          "& img, & svg": {
            height: 16,
            width: 16,
          },
        }}
      >
        <ExternalFavicon faviconUrl={faviconUrl} alt={name} sx={{ display: "flex" }} />
        <Typography
          sx={{
            fontSize: 12,
            color: "grey.300",
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
            overflow: "hidden",
          }}
        >
          {name}
        </Typography>
      </Box>
      <Typography
        sx={{
          color: "text.secondary",
          overflowY: "hidden",
          display: "-webkit-box",
          overflowWrap: "break-word",
          WebkitBoxOrient: "vertical",
          WebkitLineClamp: isMobile ? 5 : 3,
        }}
      >
        {removeSnippetEllipsisSpace(body)}
      </Typography>
    </Link>
  )
}
export { ExternalAuditCard }
